* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.logo_Img {
  width: 400px;
  height: 100px;
  margin: 30px;
}

.jeanBody {
  margin: 150px;
  /* font-family: 'Noto Sans KR', sans-serif; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.jeanBody::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.login_form,
.join_form {
  position: relative;
  z-index: 2;
}

h1 {
  text-align: center;
  margin-bottom: 60px;
  /* font-family: 'Noto Sans KR', sans-serif;
    font-family: 'Rock 3D', cursive; */
  font-size: 50px;
  color: #000;
}
h1 a {
  text-decoration: none;
  color: #000;
}

.int_area {
  width: 400px;
  position: relative;
}
.int_area:first-child {
  margin-top: 0;
}
.int_area input {
  width: 100%;
  padding: 20px 10px 10px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #999;
  font-size: 18px;
  color: #000;
  outline: none;
}
.int_area label {
  position: absolute;
  left: 10px;
  top: 15px;
  font-size: 15px;
  color: #999;
  transition: all 0.5s ease;
}
.int_area span {
  position: relative;
  left: 10px;
  top: 15px;
  font-size: 15px;
  color: #999;
}

.int_area select {
  position: relative;
  left: 50px;
  top: 15px;
}

.int_area label #pwch,
.int_area button {
  position: relative;
  width: 50%;
}

.int_area button {
  width: 20%;
  height: 40px;
  left: 320px;
  bottom: 45px;
  background: #000;
  color: #fff;
  font-size: 20px;
  border: none;
  border-radius: 25px;
}
