.int_area input:focus + label,
.int_area input:valid + label {
  top: -1px;
  font-size: 13px;
  color: #166cea;
}

.btn_area {
  margin-top: 30px;
}
.btn_area a {
  text-decoration: none;
  color: #fff;
}
.btn_area button {
  width: 100%;
  height: 50px;
  background: #166cea;
  color: #fff;
  font-size: 20px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
}

.caption {
  float: left;
  width: 25%;
  margin-top: 20px;
  text-align: center;
}
.caption:first-child {
  margin-top: 0;
}
.caption a {
  font-size: 15px;
  color: #999;
  text-decoration: none;
}
